<template>
  <div class="bg">
    <div class="title">{{$route.query.status == 'edit' ? '资金修改' : '资金发布'}}</div>
    <br />
    <br />
    <div class="item_box">
      <i-form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="180">
        <Steps :current="active" style="margin: 0px auto; width: 800px;margin-bottom:50px;">
          <Step title="基本信息" content="请填写资金基本信息"></Step>
          <Step title="投资案例" content="投资案例"></Step>
          <Step title="团队信息" content="资金成员"></Step>
          <Step title="资金联系人" content="资金联系人"></Step>
        </Steps>
        <div v-show="active==0">
          <form-item label="资金类型" prop="type" @submit.native.prevent>
            <RadioGroup v-model="formValidate.type" size="large" style="margin-top: -5px;">
              <Radio
                label="1"
                :disabled="userAuth.auditingType == 3 || $route.query.status == 'edit'"
              >个人资金</Radio>
              <Radio
                label="2"
                :disabled="userAuth.auditingType != 2 || $route.query.status == 'edit'"
              >企业资金</Radio>
              <Radio
                label="3"
                :disabled="userAuth.auditingType != 3 || $route.query.status == 'edit'"
              >政府资金</Radio>
            </RadioGroup>
            <br />
            <router-link
              v-if="userAuth.auditingType == 1"
              to="/user/certify-index"
              style="color:#2868d1;"
            >*当前为个人认证,想发布企业/政府项目,请点击前往升级认证</router-link>
            <router-link
              v-else-if="userAuth.auditingType == 2"
              to="/user/certify-index"
              style="color:#2868d1;"
            >*当前为企业认证,政府项目,请点击前往升级认证</router-link>
            <span
              v-else="userAuth.auditingType == 3"
              to="/user/certify-index"
              style="color:#2868d1;"
            >*当前为政府认证,无法代表个人/企业发布项目</span>
          </form-item>
          <form-item
            label="选择企业"
            prop="companyId"
            @submit.native.prevent
            v-if="formValidate.type == '2'"
          >
            <i-Select v-model="formValidate.companyId" :disabled="$route.query.status == 'edit'">
              <i-Option
                v-for="(item, index) in userAuth.company"
                :key="index"
                :value="item.id"
              >{{item.companyName}}</i-Option>
            </i-Select>
          </form-item>
          <form-item label="资金名称" prop="institutions" @submit.native.prevent>
            <Input
              v-model="formValidate.institutions"
              placeholder="资金名称"
              :disabled="$route.query.status == 'edit'"
            />
          </form-item>
          <form-item label="资金资产" prop="money" @submit.native.prevent>
            <Input
              v-model="formValidate.money"
              placeholder="资金资产"
              style="width:200px;   margin-top: -1px;"
            >
              <span slot="append">万元</span>
            </Input>
          </form-item>
          <form-item label="喜好轮次" prop="loveRound" @submit.native.prevent>
            <i-Select
              v-model.number="formValidate.loveRound"
              style="width: 100px;margin-right: 10px;"
            >
              <i-Option
                v-for="(item, index) in data.rotations"
                :key="index"
                :value="item.id"
              >{{item.name}}</i-Option>
            </i-Select>
          </form-item>
          <form-item label="投资方式" prop="way" @submit.native.prevent>
            <i-Select v-model.number="formValidate.way" style="width: 100px;margin-right: 10px;">
              <i-Option
                v-for="(item, index) in data.styles"
                :key="index"
                :value="item.id"
              >{{item.name}}</i-Option>
            </i-Select>
          </form-item>
          <form-item label="资金详情" prop="dyf" @submit.native.prevent>
            <Input v-model="formValidate.dyf" type="textarea" :rows="4" placeholder="资金详情" />
          </form-item>
          <form-item label="投资领域" prop="territory" @submit.native.prevent>
            <i-Select
              multiple
              v-model="formValidate.territory"
              :disabled="$route.query.status == 'edit'"
            >
              <i-Option
                v-for="(item, index) in data.realms"
                :key="index"
                :value="item.id"
              >{{item.name}}</i-Option>
            </i-Select>
          </form-item>
          <form-item label="资金所在地址" prop="address" @submit.native.prevent>
            <Cascader
              :placeholder="$route.query.status == 'edit' && formValidate.address != ''? JSON.parse(this.formValidate.address).name : '省/市/区'"
              @on-change="address"
              :data="addList"
              style="width: 30%; float: left; margin-right:1%;"
              :disabled="$route.query.status == 'edit'"
            ></Cascader>
            <Input v-model="formValidate.detailedAddress" placeholder="详细地址" style="width: 69%;" />
          </form-item>
          <form-item label="官网地址或APP下载地址" @submit.native.prevent>
            <Input v-model="formValidate.url" placeholder="官网地址或APP下载地址" />
          </form-item>
          <form-item label="资金LOGO ( 格式: 照片 )" @submit.native.prevent>
            <label>
              <Upload
                accept="image/*"
                :action="fileconfig.fileUrl"
                :headers="fileconfig.fileHead"
                name="file"
                :on-success="logo"
                :show-upload-list="false"
              >
                <Button icon="ios-cloud-upload-outline">资金LOGO</Button>
                <div v-if="formValidate.logo != ''">
                  <p>{{formValidate.logo}}</p>
                  <p style="color: #2868d1;">*已添加,想更换直接上传新LOGO即可</p>
                </div>
              </Upload>
            </label>
          </form-item>
        </div>
        <div v-show="active==1">
          <div prop="contact_name">
            <p>
              <Button @click="addInve" type="primary" icon="md-add" ghost>添加案例</Button>
            </p>
            <br />
            <ul>
              <li v-for="(item, index) in formValidate.investment" :key="index">
                <p style="font-size:16px;margin-left:10px;">投资案例{{index+1}}</p>
                <br />
                <DatePicker
                  v-model="item.time"
                  type="date"
                  placeholder="选择时间"
                  :editable="false"
                  value="yyyy-MM-dd HH:mm:ss"
                  @on-change="item.time=$event;"
                  style="width: 200px;margin-right:20px;float: left;"
                ></DatePicker>
                <Input
                  prefix="ios-contact"
                  placeholder="案例名称"
                  style="width: auto; margin-right:20px;float:left;"
                  v-model="item.projectName"
                />
                <Input v-model="item.money" placeholder="投资金额" style="width: 200px; float:left;">
                  <span slot="append">万元</span>
                </Input>
                <div class="cl"></div>
                <p>
                  <Poptip confirm title="是否要删除,请确认?" @on-ok="deleteInve(index)">
                    <Button type="error" ghost style="margin-top:10px;">删除案例</Button>
                  </Poptip>
                </p>
                <br />
                <br />
              </li>
            </ul>
            <form-item prop="outyear" @submit.native.prevent></form-item>
            <br />
            <br />
            <br />
          </div>
        </div>
        <div v-show="active==2">
          <div prop="contact_name">
            <p>
              <Button @click="addMember" type="primary" icon="md-add" ghost>添加成员</Button>
            </p>
            <br />
            <ul>
              <li v-for="(item, index) in formValidate.group" :key="index">
                <p style="font-size:16px;margin-left:10px;">成员{{index+1}}</p>
                <br />
                <Input
                  prefix="ios-contact"
                  placeholder="称呼"
                  style="width: auto; margin-right:20px;"
                  v-model="item.name"
                />
                <Input v-model="item.position" placeholder="职位" style="width: auto" />
                <br />
                <Input
                  v-model="item.introduce"
                  placeholder="介绍"
                  style="width: 600px;margin-top:10px;"
                  type="textarea"
                />
                <p>
                  <Poptip confirm title="是否要删除,请确认?" @on-ok="deleteMember(index)">
                    <Button type="error" ghost style="margin-top:10px;">删除成员</Button>
                  </Poptip>
                </p>
                <br />
                <br />
              </li>
            </ul>
            <form-item prop="outyear" @submit.native.prevent></form-item>
            <br />
            <br />
            <br />
          </div>
        </div>
        <div v-show="active==3">
          <form-item label="资金联系人姓名" prop="contactName" @submit.native.prevent>
            <Input v-model="formValidate.contactName" placeholder="资金联系人姓名" />
          </form-item>
          <form-item label="资金联系号码" prop="contactPhone" @submit.native.prevent>
            <Input v-model.number="formValidate.contactPhone" placeholder="资金联系号码" />
          </form-item>
          <form-item label="资金联系人邮箱" @submit.native.prevent>
            <Input v-model="formValidate.contactEmail" placeholder="资金联系人邮箱" />
          </form-item>
        </div>
        <form-item>
          <Button type="primary" style="margin-right:20px;" v-if="active != 0" @click="active--">上一步</Button>
          <Button type="primary" v-if="active != 3" @click="handleSubmit('formValidate')">下一步</Button>
          <Button
            type="primary"
            v-if="active == 3"
            @click="handleSubmit('formValidate')"
          >{{$route.query.status == 'edit' ? '修改资金' : '创建资金'}}</Button>
        </form-item>
      </i-form>
    </div>
  </div>
</template>
<script>
import { getRealmTag } from "../../../api/public";
import { money, moneyUser, moneyEdit } from "../../../api/capital";
import { mapActions, mapState } from "vuex";
import {
  messages,
  fileUploadUrl,
  validateNub,
  validatePhone
} from "../../../utils";
import { areasList } from "../../../areas-list";
import { getPassAuditing } from "../../../api/user";
import {
  Form,
  Radio,
  FormItem,
  Step,
  Select,
  OptionGroup,
  Cascader,
  Upload,
  Button,
  Input,
  Poptip,
  RadioGroup,
  Option,
  DatePicker,
  Steps
} from "iview";
export default {
  components: {
    [Form.name]: Form,
    [Radio.name]: Radio,
    [FormItem.name]: FormItem,
    [Step.name]: Step,
    [Select.name]: Select,
    [OptionGroup.name]: OptionGroup,
    [Cascader.name]: Cascader,
    [Upload.name]: Upload,
    [Button.name]: Button,
    [Option.name]: Option,
    [Poptip.name]: Poptip,
    [RadioGroup.name]: RadioGroup,
    [Input.name]: Input,
    DatePicker,
    [Steps.name]: Steps
  },
  computed: {
    ...mapState("user", ["userInfo"])
  },
  data() {
    return {
      // 文件上传配置
      fileconfig: {
        fileUrl: fileUploadUrl,
        fileHead: {
          token: document.cookie.match(
            new RegExp("(^| )" + "token" + "=([^;]*)(;|$)")
          )[2]
        }
      },
      //用户角色权限信息
      userAuth: "",
      //第几步 0基础 1资金进度 2 团队 3 资金联系人
      active: 0,
      //页面进入获取数据 融资列表 领域列表
      data: [],
      //省市区列表data
      addList: areasList,
      // 提交信息
      formValidate: {
        //第一步
        type: "",
        companyId: "",
        institutions: "",
        money: "",
        loveRound: "",
        way: "",
        dyf: "",
        territory: "",
        address: "",
        detailedAddress: "",
        url: "",
        logo: "",
        // 第二步
        investment: [
          {
            time: "",
            money: "",
            projectName: ""
          }
        ],
        // 第三步
        group: [
          {
            name: "",
            position: "",
            introduce: ""
          }
        ],
        //第四步
        contactName: "",
        contactPhone: "",
        contactEmail: ""
      },
      //表单验证
      ruleValidate: {
        // 第一步
        type: [
          { required: true, message: "资金类型,不能为空", trigger: "change" }
        ],
        companyId: [
          {
            required: true,
            message: "企业,不能为空",
            trigger: "blur",
            type: "number"
          }
        ],
        institutions: [
          { required: true, message: "资金名称,不能为空", trigger: "blur" }
        ],
        money: [
          {
            required: true,
            message: "资产金额不能为空,必须为数字,最多两位小数",
            trigger: "blur",
            validator: validateNub
          }
        ],
        loveRound: [
          {
            required: true,
            message: "喜好轮次,不能为空",
            trigger: "change",
            type: "number"
          }
        ],
        way: [
          {
            required: true,
            message: "投资方式,不能为空",
            trigger: "change",
            type: "number"
          }
        ],
        dyf: [
          { required: true, message: "资金详情,不能为空", trigger: "blur" }
        ],
        territory: [
          {
            required: true,
            type: "array",
            message: "最少选择一个领域,不能为空",
            trigger: "change"
          }
        ],
        address: [
          { required: true, message: "省/市/区,不能为空", trigger: "blur" }
        ],
        contactName: [
          { required: true, message: "联系人,不能为空", trigger: "blur" }
        ],
        contactPhone: [
          {
            required: true,
            message: "资金联系方式不正确,请从新输入",
            trigger: "blur",
            validator: validatePhone
          }
        ]
      }
    };
  },
  methods: {
    ...mapActions("user", ["getUserInfo"]),
    /**
     * @function 省市区监听
     */
    address(value, selectedData) {
      let selectedDatas = selectedData.pop();
      this.formValidate.address = JSON.stringify({
        name: selectedDatas.__label,
        value: selectedDatas.__value
      });
    },
    //上传lOGO
    logo(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.logo = res.data.url;
      }
    },
    //添加成员
    addMember() {
      this.formValidate.group.push({
        name: "",
        position: "",
        introduce: ""
      });
    },
    //添加案例
    addInve() {
      this.formValidate.investment.push({
        time: "",
        money: "",
        projectName: ""
      });
    },
    // 删除成员
    deleteMember(index) {
      this.formValidate.group.splice(index, 1);
    },
    // 删除案例
    deleteInve(index) {
      this.formValidate.investment.splice(index, 1);
    },
    handleSubmit(name) {
      let _this = this;
      switch (_this.active) {
        case 0:
          var fieldsToValidate =
            _this.formValidate.type == "2"
              ? [
                  "type",
                  "companyId",
                  "institutions",
                  "money",
                  "loveRound",
                  "way",
                  "dyf",
                  "territory",
                  "address"
                ]
              : [
                  "type",
                  "institutions",
                  "money",
                  "loveRound",
                  "way",
                  "dyf",
                  "territory",
                  "address"
                ];
          Promise.all(
            fieldsToValidate.map(item => {
              let p = new Promise(function(resolve, reject) {
                _this.$refs["formValidate"].validateField(item, error => {
                  resolve(error);
                });
              });
              return p;
            })
          ).then(data => {
            if (data.join("").length == 0) {
              _this.active++;
            } else {
              messages.error(`有信息必须填写，请完善填写的信息`);
            }
          });
          break;
        case 1:
          for (const item of this.formValidate.investment) {
            if (item.time == "" || item.money == "" || item.projectName == "") {
              return messages.error(
                "时间,金额和资金名称必须填写,没有案例信息请删除成员"
              );
            } else if (/[^0-9]/.test(item.money)) {
              return messages.error("投资金额必须为数字");
            }
          }
          _this.active++;
          break;
        case 2:
          if (this.formValidate.group.length > 0) {
            for (const item of this.formValidate.group) {
              if (item.name == "" || item.position == "") {
                return messages.error(
                  "成员名字和岗位信息必须填写,没有成员信息请删除成员"
                );
              }
            }
            _this.active++;
          } else {
            messages.error("团队成员必须要有一位");
          }
          break;
        case 3:
          if (this.$route.query.status == "edit") {
            this.$refs[name].validate(
              function(valid) {
                if (valid) {
                  moneyEdit(this.formValidate).then(res => {
                    if (res.data.status == "SUCCESS") {
                      messages.success(
                        "资金修改成功，资金重新进入审核，将在1-7日内完成审核"
                      );
                      this.$router.push("/user/capital-index");
                    }
                  });
                }
              }.bind(this)
            );
          } else {
            this.$refs[name].validate(
              function(valid) {
                if (valid) {
                  money(this.formValidate).then(res => {
                    if (res.data.status == "SUCCESS") {
                      this.getUserInfo();
                      messages.success("资金创建成功，将在1-7日内完成审核");
                      this.$router.push("/user/capital-index");
                    }
                  });
                }
              }.bind(this)
            );
          }
          break;
      }
    }
  },
  created() {
    if (this.$route.query.status != "edit" && this.userInfo.money.nowCount >= this.userInfo.money.maxCount) {
      messages.error("您的资金发布已达上限,发布更多资金请了解VIP权益");
      this.$router.push("/user/capital-index");
      return;
    }
    getPassAuditing().then(res => {
      this.userAuth = res.data.data;
      // 如果用户的角色是0
      if (this.userAuth.auditingType == 0) {
        messages.error("您还没有认证,请先完成认证才可以发布资金");
        this.$router.push("/user/certify-index");
      }
      this.formValidate.type = this.userAuth.auditingType.toString();
      getRealmTag().then(res => {
        this.data = res.data.data;
        //如果页面是修改
        if (this.$route.query.status == "edit") {
          moneyUser({ id: this.$route.query.id }).then(res => {
            this.formValidate = res.data.data;
            this.formValidate.territory = eval(
              `(${this.formValidate.territory})`
            );
            this.formValidate.way = parseInt(this.formValidate.way);
            this.formValidate.loveRound = parseInt(this.formValidate.loveRound);
          });
        }
      });
    });
  }
};
</script>
<style lang="less" scoped>
.bg {
  min-height: 600px;
  background: #ffffff;
  padding: 20px;
  .title {
    font-size: 18px;
    border-left: 5px solid #575757;
    color: #2868d1;
    padding-left: 10px;
    margin-bottom: 20px;
  }
}
</style>
